import React from 'react';
import styles from './TeamPane.module.scss';
import { people, personalPageProps } from '../../../../data';

const Profile = ({ profile }) => {
  return (
    <div className={styles.profile}>
      <div className={styles.profileImage}>
        <img src={profile.photo} alt="profile" />
        <div>
          {personalPageProps.map(props => {
            if (profile[props.key]) {
              return (
                <a
                  href={profile[props.key]}
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  <props.icon />
                </a>
              );
            }
            return null;
          })}
        </div>
      </div>
      <div>
        {profile.firstName} {profile.lastName}
      </div>
      <div>{profile.role}</div>
    </div>
  );
};

const TeamPane = () => {
  return (
    <div className={styles.team}>
      <div className={styles.noto}>MEET THE PEOPLE</div>
      <div>Meet the Dream Team</div>
      <div>
        A team of engineers, expert researchers, and problem solvers <br />
        set out to change the way machines understand videos.
      </div>
      <div>
        {people.map(profile => (
          <Profile profile={profile} />
        ))}
      </div>
    </div>
  );
};

export default TeamPane;
