import React from 'react';
import styles from './Clock.module.scss';

const CircleIndicator = ({ label, value }) => {
  return (
    <div className={styles.circleIndicator}>
      <div>{value}</div>
      <div>{label}</div>
    </div>
  );
};

const Clock = ({ target }) => {
  const [remainingSeconds, setRemainingSeconds] = React.useState(0);

  React.useEffect(() => {
    const updateRemainingSeconds = () => {
      const now = new Date();
      if (now < target) {
        setRemainingSeconds((target - now) / 1000);
      }
    };
    const interval = setInterval(updateRemainingSeconds, 1000);
    updateRemainingSeconds();
    return () => clearInterval(interval);
  }, [target]);

  return (
    <div className={styles.clock}>
      <CircleIndicator
        label="days"
        value={Math.floor(remainingSeconds / 86400)}
      />
      <CircleIndicator
        label="hours"
        value={Math.floor((remainingSeconds % 86400) / 3600)}
      />
      <CircleIndicator
        label="mins"
        value={Math.floor((remainingSeconds % 3600) / 60)}
      />
      <CircleIndicator label="secs" value={Math.floor(remainingSeconds % 60)} />
    </div>
  );
};

export default Clock;
